import React, { Component } from "react";
import Axios from "axios";
import styled from "styled-components";
import { Link } from "react-router-dom";

const Sprite = styled.img`
  width: 10em;
  height: 10em;
`;

const StyledTitle = styled.p`
  font-weight: bold;
`;

const StyledSectionTitle = styled.p`
  font-weight: bold;
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 7px;
`;

const StyledButton = styled.button`
  border-style: solid;
  border-color: #ff452b;
  padding: 6px 32px;
  border-radius: 24px/50%;
  cursor: pointer;
  color: #263238;
  &:hover {
    background-color: #ff452b;
    color: #fff;
  }
`;

const StyledAttributeTitle = styled.p`
  margin-bottom: 8px;
  font-weight: bold;
`;

const StyledAttribute = styled.p`
  margin-bottom: 8px;
`;

const TYPE_COLORS = {
  bug: "B1C12E",
  dark: "4F3A2D",
  dragon: "755EDF",
  electric: "FCBC17",
  fairy: "F4B1F4",
  fighting: "823551D",
  fire: "E73B0C",
  flying: "A3B3F7",
  ghost: "6060B2",
  grass: "74C236",
  ground: "D3B357",
  ice: "A3E7FD",
  normal: "C8C4BC",
  poison: "934594",
  psychic: "ED4882",
  rock: "B9A156",
  steel: "B5B5C3",
  water: "3295F6",
};

export default class Pokemon extends Component {
  state = {
    name: "",
    pokemonIndex: "",
    imageUrl: "",
    types: [],
    description: "",
    statTitleWidth: 3,
    statBarWidth: 9,
    stats: {
      hp: "",
      attack: "",
      defense: "",
      speed: "",
      specialAttack: "",
      specialDefense: "",
    },
    height: "",
    weight: "",
    eggGroups: "",
    catchRate: "",
    abilities: "",
    genderRatioMale: "",
    genderRatioFemale: "",
    evs: "",
    hatchSteps: "",
    themeColor: "#FF452B",
  };

  async componentDidMount() {
    const { pokemonIndex } = this.props.match.params;

    const pokemonUrl = `https://pokeapi.co/api/v2/pokemon/${pokemonIndex}/`;
    const pokemonSpeciesUrl = `https://pokeapi.co/api/v2/pokemon-species/${pokemonIndex}/`;

    const pokemonRes = await Axios.get(pokemonUrl);

    const name = pokemonRes.data.name;
    const imageUrl = `https://projectpokemon.org/images/normal-sprite/${name}.gif`;

    let { hp, attack, defense, speed, specialAttack, specialDefense } = "";

    pokemonRes.data.stats.map((stat) => {
      switch (stat.stat.name) {
        case "hp":
          hp = stat["base_stat"];
          break;
        case "attack":
          attack = stat["base_stat"];
          break;
        case "defense":
          defense = stat["base_stat"];
          break;
        case "speed":
          speed = stat["base_stat"];
          break;
        case "special-attack":
          specialAttack = stat["base_stat"];
          break;
        case "special-defense":
          specialDefense = stat["base_stat"];
          break;
        default:
          break;
      }
      return null;
    });

    const height = pokemonRes.data.height / 10;

    const weight = pokemonRes.data.weight / 10;

    const types = pokemonRes.data.types.map((type) => type.type.name);

    const themeColor = `${TYPE_COLORS[types[types.length - 1]]}`;

    const abilities = pokemonRes.data.abilities
      .map((ability) => {
        return ability.ability.name
          .toLowerCase()
          .split("-")
          .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
          .join(" ");
      })
      .join(", ");

    const evs = pokemonRes.data.stats
      .filter((stat) => {
        if (stat.effort > 0) {
          return true;
        }
        return false;
      })
      .map((stat) => {
        return `${stat.effort} ${stat.stat.name
          .toLowerCase()
          .split("-")
          .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
          .join(" ")}`;
      })
      .join(", ");

    await Axios.get(pokemonSpeciesUrl).then((res) => {
      let description = "";
      res.data.flavor_text_entries.some((flavor) => {
        if (flavor.language.name === "es") {
          description = flavor.flavor_text;
        }
        return null;
      });
      const femaleRate = res.data["gender_rate"];
      const genderRatioFemale = 12.5 * femaleRate;
      const genderRatioMale = 12.5 * (8 - femaleRate);

      const catchRate = Math.round((100 / 255) * res.data["capture_rate"]);

      const eggGroups = res.data["egg_groups"]
        .map((group) => {
          return group.name
            .toLowerCase()
            .split(" ")
            .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
            .join(" ");
        })
        .join(", ");

      const hatchSteps = 255 * (res.data["hatch_counter"] + 1);

      this.setState({
        description,
        genderRatioFemale,
        genderRatioMale,
        catchRate,
        eggGroups,
        hatchSteps,
      });
    });

    this.setState({
      imageUrl,
      pokemonIndex,
      name,
      types,
      stats: {
        hp,
        attack,
        defense,
        speed,
        specialAttack,
        specialDefense,
      },
      themeColor,
      height,
      weight,
      abilities,
      evs,
    });
  }

  render() {
    return (
      <div className="col">
        <div className="card">
          <div className="card-header pb-0">
            <div className="row">
              <div className="col-5">
                <StyledTitle className="mb-2">{`Pokemon #${
                  this.state.pokemonIndex
                }`}</StyledTitle>
              </div>
              <div className="col-7">
                <div className="float-right">
                  {this.state.types.map((type) => (
                    <span
                      key={type}
                      className="badge badge-pill mr-1"
                      style={{
                        backgroundColor: `#${TYPE_COLORS[type]}`,
                        color: "white",
                      }}
                    >
                      {type
                        .toLowerCase()
                        .split(" ")
                        .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
                        .join(" ")}
                    </span>
                  ))}
                </div>
              </div>
            </div>
          </div>
          <div className="card-body">
            <div className="row align-items-center">
              <div className="col-md-3 d-flex justify-content-center align-items-center">
                <Sprite
                  src={this.state.imageUrl}
                  className="card-img-top rounded mx-auto"
                  alt={`Imagen del pokemon ${this.state.name}`}
                />
              </div>
              <div className="col-md-9">
                <h4 className="mx-auto">
                  {this.state.name
                    .toLowerCase()
                    .split(" ")
                    .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
                    .join(" ")}
                </h4>
                <div className="row align-items-center">
                  <div className={`col-12 col-md-${this.state.statTitleWidth}`}>
                    Puntos de Salud
                  </div>
                  <div className={`col-12 col-md-${this.state.statBarWidth}`}>
                    <div className="progress">
                      <div
                        className="progress-bar "
                        role="progressbar"
                        style={{
                          width: `${this.state.stats.hp}%`,
                          backgroundColor: `${this.state.themeColor}`,
                        }}
                        aria-valuenow="25"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.stats.hp}
                      >
                        <small>{this.state.stats.hp}</small>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row align-items-center">
                  <div className={`col-12 col-md-${this.state.statTitleWidth}`}>
                    Ataque
                  </div>
                  <div className={`col-12 col-md-${this.state.statBarWidth}`}>
                    <div className="progress">
                      <div
                        className="progress-bar"
                        role="progressbar"
                        style={{
                          width: `${this.state.stats.attack}%`,
                          backgroundColor: `${this.state.themeColor}`,
                        }}
                        aria-valuenow="25"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.stats.attack}
                      >
                        <small>{this.state.stats.attack}</small>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row align-items-center">
                  <div className={`col-12 col-md-${this.state.statTitleWidth}`}>
                    Defensa
                  </div>
                  <div className={`col-12 col-md-${this.state.statBarWidth}`}>
                    <div className="progress">
                      <div
                        className="progress-bar "
                        role="progressbar"
                        style={{
                          width: `${this.state.stats.defense}%`,
                          backgroundColor: `${this.state.themeColor}`,
                        }}
                        aria-valuenow="25"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.stats.defense}
                      >
                        <small>{this.state.stats.defense}</small>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row align-items-center">
                  <div className={`col-12 col-md-${this.state.statTitleWidth}`}>
                    Velocidad
                  </div>
                  <div className={`col-12 col-md-${this.state.statBarWidth}`}>
                    <div className="progress">
                      <div
                        className="progress-bar"
                        role="progressbar"
                        style={{
                          width: `${this.state.stats.speed}%`,
                          backgroundColor: `${this.state.themeColor}`,
                        }}
                        aria-valuenow="25"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.stats.speed}
                      >
                        <small>{this.state.stats.speed}</small>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row align-items-center">
                  <div className={`col-12 col-md-${this.state.statTitleWidth}`}>
                    Sp Atck
                  </div>
                  <div className={`col-12 col-md-${this.state.statBarWidth}`}>
                    <div className="progress">
                      <div
                        className="progress-bar "
                        role="progressbar"
                        style={{
                          width: `${this.state.stats.specialAttack}%`,
                          backgroundColor: `${this.state.themeColor}`,
                        }}
                        aria-valuenow={this.state.stats.specialAttack}
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.stats.specialAttack}
                      >
                        <small>{this.state.stats.specialAttack}</small>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row align-items-center">
                  <div className={`col-12 col-md-${this.state.statTitleWidth}`}>
                    Sp Def
                  </div>
                  <div className={`col-12 col-md-${this.state.statBarWidth}`}>
                    <div className="progress">
                      <div
                        className="progress-bar "
                        role="progressbar"
                        style={{
                          width: `${this.state.stats.specialDefense}%`,
                          backgroundColor: `${this.state.themeColor}`,
                        }}
                        aria-valuenow={this.state.stats.specialDefense}
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.stats.specialDefense}
                      >
                        <small>{this.state.stats.specialDefense}</small>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt-1">
              <div className="col">
                <p className="mb-0 mt-3">{this.state.description}</p>
              </div>
            </div>
          </div>
          <hr className="my-1" />
          <div className="card-body">
            <StyledSectionTitle class="card-title text-center">
              Profile
            </StyledSectionTitle>
            <div className="row">
              <div className="col-md-6">
                <div className="row">
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Height:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.height} metro(s).
                    </StyledAttribute>
                  </div>
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Weight:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.weight} kg.
                    </StyledAttribute>
                  </div>
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Catch Rate:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.catchRate}%
                    </StyledAttribute>
                  </div>
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Gender Ratio:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <div class="progress">
                      <div
                        class="progress-bar"
                        role="progressbar"
                        style={{
                          width: `${this.state.genderRatioFemale}%`,
                          backgroundColor: "#c2185b",
                        }}
                        aria-valuenow="15"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.genderRatioFemale}
                      >
                        <small>{this.state.genderRatioFemale}</small>
                      </div>
                      <div
                        class="progress-bar"
                        role="progressbar"
                        style={{
                          width: `${this.state.genderRatioMale}%`,
                          backgroundColor: "#1976d2",
                        }}
                        aria-valuenow="30"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        aria-label={this.state.genderRatioMale}
                      >
                        <small>{this.state.genderRatioMale}</small>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6">
                <div className="row">
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Egg Groups:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.eggGroups}{" "}
                    </StyledAttribute>
                  </div>
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Hatch Steps:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.hatchSteps}
                    </StyledAttribute>
                  </div>
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      Abilities:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.abilities}
                    </StyledAttribute>
                  </div>
                  <div className="col-6">
                    <StyledAttributeTitle className="float-right">
                      EVs:
                    </StyledAttributeTitle>
                  </div>
                  <div className="col-6">
                    <StyledAttribute className="float-left">
                      {this.state.evs}
                    </StyledAttribute>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card-footer text-muted d-flex justify-content-center">
            <Link to="/">
              <StyledButton>Volver</StyledButton>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}
